
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1[data-v-d8ec41bc], h2[data-v-d8ec41bc] {
  font-weight: normal;
}
ul[data-v-d8ec41bc] {
  list-style-type: none;
  padding: 0;
}
li[data-v-d8ec41bc] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-d8ec41bc] {
  color: #42b983;
}
/* 主页面 */
html,body{
		width:100%;
		height:100%;
		position: fixed;
		top:0;
		left:0;
		overflow:hidden;
}
body{
		background: url(/static/img/forest.8abe2c6.png) no-repeat top center;
		background-size: 100% 100%;
		background-attachment: fixed;
}
.main-title{
		float:left;
		margin-left:10px;
		margin-top:-50px;
		color:#FFFFFF;
}
.ocean-div{
		margin-top:-20px;
}
.ocean-btn{
		width:50px;
}
.flower-div1{
		float:left;
		margin-top:40px;
}
.flower-div2{
		float:right;
		padding-right:15px;
		margin-top:40px;
}
.flower-btn{
		margin-top:10px;
		width:40px;
}
.lion{
		width:100px;
		margin-top:-10%;
		margin-left:-20%;
}
.close-btn{
		margin-top:-50px;
		float:right;
		padding-right:15px;
}
.store{
		width:120px;
		margin-top:-10%;
		margin-left:65%;
}
.gold{
		width:70px;
		margin-top:220px;
		margin-left:160px;
}
.icon-font{
		display:block;
		font-size:12px;
		margin-top:-10px;
}
/* 道具弹出框css */
.popup0 {
		padding-bottom:15px;
		height:400px;
		background-color:#FFFFFF;
}
.toolcard{
	height: 400px;
	background-color:#FFFFFF;
	overflow-y: auto;
}
/* 道具弹出框css */
.ground-body{
	width:100%;
	height:100%;
	position:relative;
	overflow: hidden;/* 防止父级一起移动 */
}

